let medical = () => {
  $(".category-btn").on("click", () => {
    $(".category-box").show();
  });
  $(".medical-main-carousel").slick({
    infinite: true,
    dots: false,
    arrows: false,
    autoplay: true,
    slidesToShow: 1,
    asNavFor: $(".medical-dot-carousel"),
  });
  $(".medical-dot-carousel").slick({
    infinite: true,
    dots: false,
    arrows: false,
    autoplay: true,
    slidesToShow: 7,
    centerMode: true,
    asNavFor: $(".medical-main-carousel"),
    responsive: [
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 3,
        },
      },
    ],
  });
  $("#medical-pre").on("click", () => {
    $(".medical-dot-carousel").slick("slickPrev");
  });
  $("#medical-next").on("click", () => {
    $(".medical-dot-carousel").slick("slickNext");
  });
};
